<template>
  <div>
    <div class="searchs">
      <el-form :inline="true" @submit.native.prevent="onSubmit" :model="searchs" class="form-search">
        <el-row>
          <el-col>
            <el-form-item label="项目名称">
              <el-input v-model="searchs.name" placeholder="项目查询"></el-input>
            </el-form-item>
            <el-form-item label="类型">
              <el-select v-model="searchs.type" clearable placeholder="请选择">
                <el-option v-for="(value, name) in config.type" :key="name" :label="value" :value="name"> </el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="状态">
              <el-select v-model="searchs.status" clearable placeholder="请选择">
                <el-option v-for="(value, name) in config.status" :key="name" :label="value" :value="name"> </el-option>
              </el-select>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" native-type="submit">查询</el-button>
              <el-button type="primary" @click="removeAll">批量关闭</el-button>
              <router-link to="/itemdq/create">
                <el-button style="margin-left: 8px;" type="success">创建</el-button>
              </router-link>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
    </div>
    <el-table :data="tableData" class="list" v-loading="loading" @selection-change="handleSelectionChange">
      >
      <el-table-column type="selection" width="55" />
      <el-table-column prop="id" label="编号" align="center" />
      <el-table-column prop="name" label="项目名" align="center" width="150" />
      <el-table-column prop="money" label="项目金额" align="center" />
      <el-table-column prop="apr" label="利率" align="center" />
      <el-table-column prop="pack" label="红包" align="center" />
      <el-table-column prop="stock" label="总库存" align="center" />
      <el-table-column prop="rem_count" label="剩余库存" align="center" />
      <el-table-column prop="buy_count" label="已售数量" align="center" />
      <el-table-column prop="top_apr" label="上级分润" align="center" />
      <el-table-column prop="schedule" label="进度" align="center" />
      <el-table-column prop="min_money" label="起投金额" align="center" />
      <el-table-column prop="days" label="天数" align="center" />
      <el-table-column prop="sum_money" label="已投金额" align="center" />
      <el-table-column prop="type_name" label="类型" align="center" />
      <el-table-column prop="is_show_index_name" label="首页显示" align="center" />
      <el-table-column prop="status_name" label="状态" align="center" />
      <el-table-column prop="sort" label="排名" align="center" />
      <el-table-column prop="addtime_date" label="添加时间" align="center" width="110" />
      <el-table-column prop="address" align="center" label="操作" width="270">
        <template slot-scope="scope">
          <el-button type="primary" size="small" @click="eidt(scope.row.id)">编辑</el-button>
          <el-button type="primary" size="small" @click="backMoney(scope.row.id)">返现</el-button>
          <el-button type="danger" size="small" @click="remove(scope.row.id)">关闭</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      background
      layout="total, prev, pager, next"
      :total="count"
      :current-page="page_curren"
      :hide-on-single-page="false"
      @current-change="currentChange"
      class="pagination"
    />
  </div>
</template>
<script>
import Fecth from '../../utils/fetch'
import { Message } from 'element-ui'

export default {
  data() {
    return {
      loading: false,
      searchs: {
        name: '',
        status: '',
        type: '',
      },
      tableData: [],
      count: 0,
      page_curren: 1,
      config: {
        type: null,
        status: null,
      },
      selectIds: [],
    }
  },
  mounted() {
    this.FetchList()
  },
  methods: {
    removeAll() {
      if (this.selectIds.length < 1) {
        Message.error('未选中数据')
        return
      }
      this.loading = true
      Fecth('/item/dq/close', { id: this.selectIds })
        .then(() => {
          this.FetchList()
          this.loading = false
          Message.success('删除成功')
        })
        .catch(() => (this.loading = false))
    },
    handleSelectionChange(val) {
      this.selectIds = []
      val.forEach(item => {
        this.selectIds.push(item.id)
      })
    },
    remove(id) {
      this.loading = true
      Fecth('/item/dq/close', { id: [id] })
        .then(() => {
          this.FetchList()
          this.loading = false
          Message.success('关闭成功')
        })
        .catch(() => (this.loading = false))
    },
    eidt(id) {
      this.$router.push('/itemdq/rule/' + id)
    },
    FetchList() {
      this.loading = true
      Fecth('/item/dq/search', {
        page_curren: this.page_curren,
        ...this.searchs,
      })
        .then(r => {
          this.loading = false
          this.count = r.data.count
          this.page_curren = Math.floor(r.data.page_curren)
          this.tableData = r.data.list
          this.config = r.data.config
        })
        .catch(() => (this.loading = false))
    },
    currentChange(page) {
      this.page_curren = page
      this.FetchList()
    },
    onSubmit() {
      this.page_curren = 1
      this.FetchList()
    },
    backMoney(id) {
      this.$confirm('此操作将会返还所有投资该项目的会员本金', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
      }).then(() => {
        this.loading = true
        Fecth('/item/dq/backmoney', {
          id: id,
        })
          .then(() => {
            this.loading = false
            Message.success('处理成功')
          })
          .catch(() => (this.loading = false))
      })
    },
  },
}
</script>
